.container{
    display: grid;
    grid-template-columns: 10% 1fr;
    align-items: center;
    padding: 10px;
}
.itemLogo{
    aspect-ratio: 1;
}
.category{
    display: flex;
    justify-content: space-between;
    align-items: last baseline;
    margin-bottom: 10px;
    color: #b0b0b0fd;
    border-bottom: 1px solid #3ce7bc4b;
    text-transform: capitalize;
}
.title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}
.title p{
    color: #8e9090fd;
    font-size: 17px; 
}
.title h1{
    width: 50%;
    color: #3c9ae7fd;
    font-size: 26px;
    text-transform: capitalize;
}
.counter{
    display: flex;
    
}
.counter :first-child,
.counter :last-child{
    width: 23px;
    height: 23px;
    border: none;
    color: #1ab9924b;
    font-size: 16px;
    font-weight: 900;
    background-color: transparent;
    transform: scale(1, 1);
    transition: transform 0.5s;
}
.counter :first-child:hover,
.counter :last-child:hover{
    color: #047ae14b;
    transform: scale(1.3, 1.3);
    background-color: #3ce7bc4b;
    border-radius: 50%;
}
.counter p{
    min-width: 20px;
    text-align: center;
    color: #8e9090fd;
}
.delBtn{
    width: 30px;
    height: 30px;
    align-self: flex-end;
    border: none;
    cursor: pointer;
    color: rgb(36, 153, 203);
    font-weight: bold;
    background-color: transparent;
    border-radius: 50%;
    transition: transform 1s ease;
    transition: background-color 1s;
    transition: color 0.7s ease-in-out;
}
.delBtn:hover{
    color: #ffffff;
    background-color: #00abc593;
    transform: scale(1.1,1.1);
}